import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListItemAvatar from "@mui/material/ListItemAvatar";

const dispatchedEvents = [];

const SrsMuiListItemAvatar = function (props, ref) {
  return (
    <ListItemAvatar {...props} theme={props.muiTheme}>
      <slot />
    </ListItemAvatar>
  );
};

const styles = `
  /* Custom styles for the MuiListItemAvatar component */
`;

const props = [
  {
    name: "className",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ListItemAvatar if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListItemAvatar),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
